<template>
	<view>
		<view class="head">
			<!-- <image v-if="user.sex == 1" src="../../static/头像 男孩.png" mode="widthFix" class="headpic"></image>
			<image v-else src="../../static/头像 女孩.png" mode="widthFix" class="headpic"></image> -->
			<image :src="user.head_pic" mode="widthFix" class="headpic"></image>
			<text class="headtext">{{user.name}}</text>
		</view>
		<view class="messageBox">
			<view class="username-all" @click="gotoChangeUsername">
				<view class="all-both">
					<text class="all-both-first">用户名</text>
					<text class="all-both-second">{{user.name}}</text>
				</view>
				<uni-icons type="arrowright" size="16"></uni-icons>
			</view>
			<view class="sex-all" @click="gotoChangeSex">
				<view class="all-both">
					<text class="all-both-first">性别</text>
					<view class="all-both-second" v-if="user.sex === 1">男生</view>
					<view class="all-both-second" v-else-if="user.sex === 0">女生</view>
					<view class="all-both-second" v-else>未知</view>
				</view>
				<uni-icons type="arrowright" size="16"></uni-icons>
			</view>
			<view class="jianjie-all" @click="gotoChangeIntroduction">
				<view class="all-both">
					<text class="all-both-first">简介</text>
					<text class="all-both-second">{{user.introduction}}</text>
				</view>
				<uni-icons type="arrowright" size="16"></uni-icons>
			</view>
		</view>
		<view class="bottombox">
			<view class="exitbox" @click="exit">
				<text class="exit_text">退出登录</text>
			</view>
		</view>
	</view>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	export default {
		computed:{
			...mapState('m_user', ['user'])
		},
		data() {
			return {
				
			};
		},
		methods: {
			...mapMutations('m_user', ['logoutToken']),
			gotoChangeUsername(){
				uni.navigateTo({
					url: '/subpkg/ChangeUsername/ChangeUsername'
				})
			},
			gotoChangeSex(){
				uni.navigateTo({
					url: '/subpkg/changeSex/changeSex'
				})
			},
			gotoChangeIntroduction(){
				uni.navigateTo({
					url: '/subpkg/changeIntroduction/changeIntroduction'
				})
			},
			exit(){
				return uni.showModal({
						title: '登出',
						content: '确认要退出登录吗？',
						success: function(res) {
						if (res.confirm) {
						    this.logoutToken()
							uni.switchTab({
								url: '/pages/usr/usr'
							})
						} 
						else { 
							
						}
					}.bind(this)
				})
			}
		}
	}
</script>

